.NavBar{
    height: 100vh;
    
    .nav{
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
       

        .profile{
            width: 100%;
            text-align: center;
            margin-top: 1rem;
            border-bottom: 1px solid #2e344e;

            img{
                width: 80%;
                border-radius: 50%;
                border: 5px solid #2e344e;
                margin-bottom: 1rem;
                //margin-top: ;
            }
        }

        .nav-items{
            width: 100%;
            .nav-item {
                list-style:  none;
                text-align: center;
                a{
                    text-decoration: none;
                    font-size: inherit;
                    color: #707070;
                    display: block;
                    padding: 0.5rem 0;
                    position: relative;
                    &::before{
                        content: "";
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 0.5rem;
                        height: 100%;
                        background-color: #0381ff33;
                        transform-origin: bottom;
                        transform: scale(0);
                        overflow-x: hidden;
                        transition:  transform .6s, .2s, width .5s cubic-bezier(.94,.02,.27,1.07);
                    }

                    &:hover::before{
                        width: 100%;
                        transform: scale(1);
                    }
                }
            }
        }

        .footer{
            width: 100%;
            border-top: 1px solid #2e344e;
            p{
                text-align: center;
                padding: 1rem 0;
            }
        }
    }
}

.active{
    background-color: #333;
}